/*====================================================================
                    common style
====================================================================*/
* {
  font-size: 16px;
  color: #333333;
  font-family: "ヒラギノ明朝 ProN", serif;
  line-height: 1.6875;
  letter-spacing: 1.1px;
}

img {
  height: 100%;
  width: 100%;
}

.inner {
  max-width: 980px;
  margin: 0 auto;
  padding: 0 40px;
}

@media screen and (max-width: 768px) {
  .inner {
    padding: 0 20px;
  }
}

a {
  text-decoration: none;
  color: #333333;
  cursor: pointer;
}

a:hover {
  opacity: .7;
}

.sectionTitleBox {
  width: 100%;
  text-align: center;
  position: relative;
  z-index: 0;
  background-size: cover;
  margin-bottom: 60px;
}

@media screen and (max-width: 768px) {
  .sectionTitleBox {
    margin-bottom: 30px;
  }
}

.sectionBg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 150px 40px;
}

@media screen and (max-width: 768px) {
  .sectionBg {
    padding: 80px 20px;
  }
}

@media screen and (max-width: 520px) {
  .sectionBg {
    padding: 50px 20px;
  }
}

.sectionTitle {
  z-index: 100;
  font-size: 48px;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .sectionTitle {
    font-size: 32px;
  }
}

@media screen and (min-width: 769px) {
  main {
    padding-top: 145px;
  }
}

.subTitle {
  font-size: 28px;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .subTitle {
    font-size: 20px;
  }
}

.subTitle span {
  display: block;
  margin-top: 2px;
  height: 1px;
  width: 150px;
  background-color: #000000;
  margin-bottom: 60px;
}

@media screen and (max-width: 768px) {
  .subTitle span {
    margin-bottom: 30px;
    width: 100px;
  }
}

.nexPageBox {
  text-align: right;
  margin-top: 60px;
}

@media screen and (max-width: 768px) {
  .nexPageBox {
    margin-top: 30px;
  }
}

.nexPage {
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 4px 4px 4px 10px;
  margin-right: 20px;
  position: relative;
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  .nexPage {
    font-size: 12px;
  }
}

.nexPage:hover {
  text-decoration: none;
}

.nexPage span {
  position: absolute;
  transform: translate(20%, -11%);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 17.5px 0 17.5px 20px;
  border-color: transparent transparent transparent rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 768px) {
  .nexPage span {
    transform: translate(26.7%, -14%);
    border-width: 14px 0 14px 15px;
  }
}

/*====================================================================
                    header
====================================================================*/
.header {
  position: fixed;
  background-color: #FFFFFF;
  width: 100%;
  z-index: 100;
}

@media screen {
  .header {
    padding: 40px;
    top: 0;
    left: 0;
    right: 0;
  }
}

@media screen and (max-width: 768px) {
  .header {
    top: auto;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 15px;
  }
}

@media screen {
  .headerRes {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .headerRes {
    display: block;
    padding: 10px 20px;
  }
}

.headerTitleLinkRes {
  padding: 5px;
}

.headerTitleLinkRes .headerTitle {
  font-size: 26px;
  display: inline-block;
}

.headerInner {
  margin: 0 auto;
  max-width: 980px;
}

@media screen and (min-width: 769px) {
  .headerInner {
    display: flex;
  }
}

@media screen and (max-width: 768px) {
  .headerInner {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .headerTitleLink {
    display: none;
  }
}

.headerTitle {
  font-size: 38px;
  font-weight: bold;
}

@media screen and (min-width: 769px) {
  .headerNav {
    margin-left: auto;
  }
}

.navLIst {
  display: flex;
}

@media screen and (max-width: 768px) {
  .navLIst {
    justify-content: center;
  }
}

@media screen and (min-width: 769px) {
  .navItem + .navItem {
    margin-left: 20px;
  }
}

@media screen and (max-width: 768px) {
  .navItem + .navItem {
    margin-left: 3%;
  }
}

.navLink {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 18px;
}

@media screen and (max-width: 768px) {
  .navLink {
    font-size: 14px;
  }
}

.navLink svg {
  margin: 0 auto;
}

/*====================================================================
                    top
====================================================================*/
.top {
  text-align: left;
}

@media screen and (max-width: 768px) {
  .top {
    background-position: right;
  }
}

.bgTopInner {
  max-width: 980px;
  margin: 0 auto;
}

.sectionTopTitle {
  font-weight: bold;
  color: #FFFFFF;
  letter-spacing: 1.5px;
  font-size: 28px;
  margin-bottom: 10px;
}

@media screen and (max-width: 768px) {
  .sectionTopTitle {
    font-size: 20px;
  }
}

@media screen and (max-width: 768px) {
  .sectionTopTitle {
    font-size: 18px;
  }
}

.sectionTopText {
  color: #FFFFFF;
  line-height: 2;
  font-weight: bold;
  font-size: 18px;
}

@media screen and (max-width: 768px) {
  .sectionTopText {
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .sectionTopText {
    font-size: 12px;
  }
}

.topGameText {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}

@media screen and (max-width: 768px) {
  .topGameText {
    font-weight: 16px;
  }
}

@media screen and (max-width: 520px) {
  .topGameText {
    font-size: 14px;
  }
}

/*====================================================================
                    game
====================================================================*/
.gameTitle {
  text-align: center;
  font-weight: bold;
  margin-top: 40px;
  font-size: 32px;
}

@media screen and (max-width: 768px) {
  .gameTitle {
    font-size: 20px;
  }
}

@media screen and (max-width: 520px) {
  .gameTitle {
    font-size: 18px;
  }
}

.gameImageBox {
  margin: 0 auto;
}

.gameImageBox img {
  width: 100%;
  height: auto;
}

@media screen and (min-width: 769px) {
  .gameImageBox {
    width: 600px;
    height: 450px;
  }
}

@media screen and (max-width: 768px) {
  .gameImageBox {
    width: 330px;
    height: 247.5px;
  }
}

.gameText {
  font-weight: bold;
  text-align: center;
  margin-top: 10px;
  font-size: 24px;
}

@media screen and (max-width: 768px) {
  .gameText {
    font-size: 16px;
  }
}

@media screen and (max-width: 520px) {
  .gameText {
    font-size: 14px;
  }
}

.gameInner {
  padding: 40px 0;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .gameInner {
    padding: 20px 0;
  }
}

.count {
  font-weight: bold;
  margin-top: 20px;
  font-size: 32px;
}

@media screen and (max-width: 768px) {
  .count {
    font-size: 20px;
    margin-top: 15px;
  }
}

.smileCount {
  color: tomato;
}

.noSmileCount {
  color: blue;
}

.finishedComment {
  font-size: 24px;
  color: tomato;
}

.boxInner {
  height: 60px;
  margin-top: 20px;
}

@media screen and (max-width: 768px) {
  .boxInner {
    height: 40px;
  }
}

.btn {
  margin: 0 auto;
  background-color: tomato;
  display: inline-block;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  font-size: 24px;
  padding: 10px 20px;
}

@media screen and (max-width: 768px) {
  .btn {
    font-size: 16px;
    padding: 6px 12px;
  }
}

.btn:active {
  margin-top: 6px;
  box-shadow: none;
}

/*====================================================================
                    about
====================================================================*/
.about {
  background-position: center;
}

@media screen {
  .aboutFirst {
    justify-content: space-between;
    display: flex;
    margin-bottom: 100px;
  }
}

@media screen and (max-width: 768px) {
  .aboutFirst {
    display: block;
    margin-bottom: 50px;
  }
}

@media screen {
  .aboutFirstPic {
    flex: 0 0 38%;
  }
}

@media screen and (max-width: 768px) {
  .aboutFirstPic {
    width: 70%;
    margin: 0 auto 40px auto;
  }
}

.aboutFirstPic img {
  border-radius: 50%;
}

@media screen and (min-width: 769px) {
  .aboutFirstList {
    flex: 0 0 58%;
    align-self: center;
  }
}

@media screen and (max-width: 768px) {
  .aboutFirstList {
    max-width: 400px;
    width: 90%;
    margin: 0 auto;
  }
}

.aboutFirstItem {
  display: flex;
}

.aboutFirstItem + .aboutFirstItem {
  margin-top: 10px;
}

.aboutFirstItemTitle {
  font-weight: bold;
}

@media screen and (min-width: 769px) {
  .aboutFirstItemTitle {
    flex: 0 0 120px;
  }
}

@media screen and (max-width: 768px) {
  .aboutFirstItemTitle {
    flex: 0 0 76px;
    font-size: 14px;
  }
}

.aboutFirstItemDesc {
  flex: 1 1 100%;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .aboutFirstItemDesc {
    font-size: 14px;
  }
}

.br {
  display: none;
}

@media screen and (max-width: 520px) {
  .br {
    display: block;
  }
}

.aboutSecond {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.skillSubTitle span {
  margin-bottom: 0;
}

.skillItem {
  flex: 0 0 45%;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  margin-top: 60px;
}

@media screen and (max-width: 768px) {
  .skillItem {
    margin-top: 30px;
  }
}

.skillImg {
  height: 300px;
  width: 300px;
  margin: 0 auto;
  padding-top: 10px;
}

.skillTitle,
.skillText {
  padding: 0 30px;
}

@media screen and (max-width: 768px) {
  .skillTitle,
  .skillText {
    padding: 0 20px;
  }
}

.skillTitle {
  font-size: 20px;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .skillTitle {
    font-size: 16px;
  }
}

.skillText {
  padding: 20px 30px;
  text-align: justify;
}

@media screen and (max-width: 768px) {
  .skillText {
    font-size: 14px;
    padding: 10px 20px 20px;
  }
}

/*====================================================================
                    works
====================================================================*/
.workItemLink {
  display: block;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  transition: all .2s ease;
}

.workItemLink + .workItemLink {
  margin-top: 70px;
}

.workItemLink:hover {
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.2);
  text-decoration: none;
  opacity: 1;
}

.workSubText {
  font-size: 14px;
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  .workSubText {
    font-size: 12px;
    margin-bottom: 10px;
  }
}

.workItemImg {
  border-bottom: solid 0.1px rgba(0, 0, 0, 0.1);
}

.workItemImg img {
  border-radius: 8px 8px 0 0;
}

.workItemLanguageBr {
  display: none;
}

@media screen and (max-width: 520px) {
  .workItemLanguageBr {
    display: block;
  }
}

.workItemTitle,
.workItemLanguage,
.workItemText {
  padding: 0 30px;
}

@media screen and (max-width: 768px) {
  .workItemTitle,
  .workItemLanguage,
  .workItemText {
    padding: 0 20px;
  }
}

.workItemTitle {
  padding-bottom: 5px;
  font-weight: bold;
  padding-top: 25px;
  font-size: 20px;
}

@media screen {
  .workItemTitle {
    padding-top: 15px;
    font-size: 16px;
  }
}

.workItemLanguage {
  width: 100%;
  font-weight: bold;
  padding-bottom: 5px;
}

@media screen and (max-width: 768px) {
  .workItemLanguage {
    font-size: 14px;
  }
}

.workItemText {
  padding-bottom: 25px;
}

@media screen and (max-width: 768px) {
  .workItemText {
    font-size: 14px;
  }
}

/*====================================================================
                    movies
====================================================================*/
.movies {
  background-position: bottom;
}

.moviesBox {
  height: 450px;
  width: 700px !important;
  margin: 0 auto;
}

.moviesBox div {
  width: 100% !important;
  height: 100% !important;
}

.video-stream {
  width: 100%;
}

/*====================================================================
                    history
====================================================================*/
.historyBox {
  display: flex;
}

.historyArrow {
  display: block;
  width: 1px;
  height: 100;
  background-color: #333333;
  position: relative;
}

.historyList {
  flex: 1 1 100%;
}

.historyItem {
  display: flex;
  position: relative;
}

@media screen and (max-width: 768px) {
  .historyItem {
    display: block;
  }
}

.historyItem + .historyItem {
  margin-top: 30px;
}

@media screen and (max-width: 768px) {
  .historyItem + .historyItem {
    margin-top: 20px;
  }
}

.historyMark {
  position: absolute;
  transform: translate(-50%, 0);
}

.arrow {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 15px 7.5px 0 7.5px;
  border-color: #333333 transparent transparent transparent;
  transform: translate(-50%, 100%);
}

.historyDate {
  font-weight: bold;
}

@media screen and (min-width: 769px) {
  .historyDate {
    margin-left: 50px;
    flex: 0 0 150px;
  }
}

@media screen and (max-width: 768px) {
  .historyDate {
    margin-bottom: 5px;
    margin-left: 30px;
  }
}

.historyText {
  flex: 1 1 100%;
}

@media screen and (max-width: 768px) {
  .historyText {
    font-size: 14px;
    margin-left: 30px;
  }
}

/*====================================================================
                    contact
====================================================================*/
.contact {
  background-position: bottom;
}

.snsList {
  justify-content: center;
}

@media screen and (max-width: 768px) {
  .snsList {
    margin-bottom: 220px;
  }
}

@media screen and (max-width: 520px) {
  .snsList {
    margin-bottom: 280px;
  }
}

.snsItem + .snsItem {
  margin-left: 30px;
}

/*====================================================================
                    footer
====================================================================*/
@media screen and (max-width: 768px) {
  .footer {
    padding-bottom: 81px;
  }
}

.footerText {
  text-align: center;
  padding: 30px 0;
}

@media screen and (max-width: 768px) {
  .footerText {
    padding: 30px 0 10px 0;
  }
}

.footerText {
  font-size: 14px;
}

@media screen and (max-width: 768px) {
  .footerText {
    font-size: 10px;
  }
}
